.header {
  display: flex;
  align-items: center;
  min-height: 50px;
  padding: 0.5rem 1rem;
  background-image: linear-gradient(284deg, #fedd4e, #fcb43a);
  color: #fff;
  margin-bottom: 1rem;
}

.header a {
  color: #fff;
  text-decoration: none;
}

.header-title {
  font-size: 1.2rem;
  font-weight: normal;
}

.header-title::after {
  content: ' â–¸ ';
  padding: 0 0.5rem;
}

.header-subtitle {
  font-size: 1.2rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.search-panel {
  display: flex;
}

.search-panel__filters {
  flex: 1;
}

.search-panel__results {
  flex: 3;
}

.ais-Highlight-highlighted {
  color: inherit;
  font-size: inherit;
}

#searchbox {
  margin-bottom: 2rem;
}

#pagination {
  margin: 2rem auto;
  text-align: center;
}

/* Search hits by Yam */

body {
  font-family: 'Arial, sans-serif';
  background-color: #f0f0f0;
}

.search-hit {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  padding: 16px;
  margin-bottom: 16px;
  border-radius: 8px;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.search-hit-header {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.search-hit-avatar {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-right: 16px;
  object-fit: cover;
}

.search-hit-info {
  flex-grow: 1;
}

.search-hit-title {
  margin: 0;
  font-size: 1.25em;
  line-height: 1.4;
}

.search-hit-link {
  text-decoration: none;
  color: #000;
}

.search-hit-meta {
  margin: 0;
  color: #666;
  font-size: 0.9em;
}

.search-hit-content {
  margin-bottom: 16px;
}

.search-hit-text {
  margin: 0;
  color: #333;
  line-height: 1.6;
}

.search-hit-footer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  border-top: 1px solid #e0e0e0;
  padding-top: 8px;
}

.search-hit-category {
  margin-right: 8px;
  color: #333;
}

.search-hit-tags {
  flex-grow: 1;
  color: #777;
  font-size: 0.9em;
}

.search-hit-read-more {
  text-align: right;
}

.search-hit-link {
  text-decoration: none;
  color: #0066cc;
}

.search-hit-tags span {
  background-color: #e0e0e0;
  padding: 2px 6px;
  border-radius: 12px;
  margin-right: 4px;
  font-size: 0.8em;
}
